body {
  margin: 0;
  font-family: 'Roboto', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #8EC5FC;
  background-image: linear-gradient(62deg, #8EC5FC 0%, #E0C3FC 100%);
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
}

section,
footer {
  padding: 1.5rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-logo {
  margin: 1rem;
  margin-right: 5rem;
  height: 5rem;
  pointer-events: none;
}

h1,
h2,
h3,
h4,
h5,
a,
.App-link {
  color: #61dafb;
}

a:hover,
.App-link:hover {
  color: #8aa725;
}

.App-header {
  text-align: left;
}

.App-title {
  flex-direction: column;
  padding-top: 10px;
}

.App-title div {
  font-size: calc(6px + 2vmin);
}

.App {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 1400px;
}

.App-card {
  background-color: #282c34;
  -webkit-box-shadow: 0 10px 6px -6px #777;
    -moz-box-shadow: 0 10px 6px -6px #777;
        box-shadow: 0 10px 6px -6px #777;
}

.Map-container {
  width: 100%;
}

.Map-container > div {
  width: 100%!important;
  height: 100%!important;
}

.Map-container .side-panel--container {
  left: -55px;
}

.b-l-primary {
  padding-left: 2rem;
  left: -1.5rem;
  position: relative;
}

.Button-container button {
  z-index: 100;
  width: 120px;
  height: 40px;
  background-color: #1f7cf4;
  color: #FFFFFF;
  cursor: pointer;
  border: 0;
  border-radius: 3px;
  margin: .25rem;
}

.carousel-item {
  text-align: center;
}

.carousel-item img {
  height: 400px;
}

.carousel-caption {
  padding: 0.5rem;
}

.Socials a {
  line-height: 1.5rem;
  font-size: 1.5rem;
}

@media (max-width: 700px) {
  body {
    font-size: 12px;
  }

  h1 {
    font-size: 1.5rem;
  }

  h3 {
    font-size: 1.25rem;
  }

  h5 {
    font-size: 1rem;
  }

  .App-header {
    text-align: right;
  }

  .App-logo {
    margin-top: .5rem;
    margin-right: .5rem;
    height: 3.5rem;
    pointer-events: none;
  }

  .Map-container {
    overflow: hidden;
    min-height: 150vh;
  }

  .Map-container .side-panel--container {
    position: relative;
    left: -10px;
    max-height: 700px;
  }

  .Button-container {
    display: none!important;
  }
}